<template>
  <div class="flex-bt">
    <Introduce :text='text' />
    <div class="viewer-c">
      <div id="viewer"></div>
    </div>

  </div>
</template>

<script>
import Introduce from '@v/demo/components/introduce.vue'
import { Viewer } from 'photo-sphere-viewer'
import MarkersPlugins from 'photo-sphere-viewer/dist/plugins/markers'
import Gyroscope from 'photo-sphere-viewer/dist/plugins/gyroscope'
import 'photo-sphere-viewer/dist/photo-sphere-viewer.css'
export default {
  components: {
    Introduce
  },
  data () {
    return {
      text: {
        title: 'codeMirror',
        main: '应用于接口微服务项目当中，该项目是通过「数据查询 mybatis」「消息共享 Redis发布订阅」「服务监控 prometheus」向用户提供接口服务，其中sql语言录入模块是一重要模块，向使用者通过了动态表单配置和sql语句两种方案，单纯使用input框的场景不仅仅外观不够美观，体验也很差，故而选择codeMirror。',
        intro: 'vue-codemirror可以通过引入不同的js包来配置语言和样式，同时为了体验更好可以引入格式化工具sqlFormatter对代码格式化',
        tips: 'F2格式化代码、Ctrl补全代码'
      }
    }
  },
  mounted () {
    this.getInit()
  },
  methods: {
    getInit (e) {
      const width = '100%'
      const height = '100%'
      const viewer = new Viewer({
        container: document.querySelector('#viewer'),
        panorama: require('@img/htc.jpg'), //  'https://nhdsj.ninghai.gov.cn/pic/htc.jpg'   require('@a/htc.jpg')
        // loadingImg: require('@a/photosphere-logo.gif'),
        sphereCorrectionReorder: true,
        plugins: [
          Gyroscope,
          [MarkersPlugins, {
            markers: e
          }]
        ],
        size: {
          width: width,
          height: height
        }
      })
      // 获取当前点击位置经纬度
      viewer.on('click', (e, data) => {
        alert(`${data.rightclick ? 'right clicked' : 'clicked'} at longitude: ${data.longitude} latitude: ${data.latitude}`)
      })
      // 点击标记事件
      const markersPlugin = viewer.getPlugin(MarkersPlugins)
      markersPlugin.on('leave-marker', (e, marker) => {
        // console.log(`Cursor is over marker ${marker.id}`)
      })
      markersPlugin.on('select-marker', (e, marker, data) => {
        this.showbox = !this.showbox
        this.list = marker.config
        this.list.title = marker.config.tooltip.content
      })
    }
  }
}
</script>

<style lang='scss' scoped>
.viewer-c {
  height: calc(100vh - 300px);
  width: 50%;
}
</style>
